import { INTERNET, PROXIMA, SET_GLOBAL } from "../global/global-reducer";
import { RESET } from "../reset/reset-reducer";


const SET_OD = 'SET_OD';


let initialState = {

    currentOd: 'Количество доступов',
    currentOdIndex: 0,
    currentNames: [
        '1 Одновременный доступ',
        '2 Одновременных доступа',
        '3 Одновременных доступа',
        '5 Одновременных доступов',
        '10 Одновременных доступов',
        '20 Одновременных доступов',
        '30 Одновременных доступов',
        '50 Одновременных доступов',
    ],
    internetNames: [
        '1 Одновременный доступ',
        '2 Одновременных доступа',
        '3 Одновременных доступа',
        '5 Одновременных доступов',
        '10 Одновременных доступов',
        '20 Одновременных доступов',
        '30 Одновременных доступов',
        '50 Одновременных доступов',
    ],
    proximaNames: [
        'Флэш Проксима',
        '1 ОД Проксима',
        '2 ОД Проксима',
        '3 ОД Проксима',
        '5 ОД Проксима',
        '10 ОД Проксима',
        '20 ОД Проксима',
        '30 ОД Проксима',
        '50 ОД Проксима',
    ],



}



//AC
const setOd = (odIndex) => ({ type: SET_OD, odIndex })


//THUNK

export const odChange = (odIndex) => (dispatch, getState) => {
    const currentComplect = getState().currentComplect
    if (!currentComplect || currentComplect.name === 'Универсальный комплект') {
        window.alert('сначала выберите комплект')
    } else {
        dispatch(setOd(odIndex))
    }
}

export const od = (state = initialState, action) => {

    switch (action.type) {
        case SET_OD:
            
            return state.currentOd !== state.currentNames[action.odIndex]
                ? { ...state, currentOd: state.currentNames[action.odIndex], currentOdIndex: action.odIndex }
                : state

        case SET_GLOBAL: //from global-parameters-reducer 

            if (action.currentSupply && action.currentSupply.title === INTERNET) {
                
                return { ...state, currentNames: state.internetNames, currentOd: state.internetNames[0] }
            } else if (action.currentSupply && action.currentSupply.title  === PROXIMA) {
                
                return { ...state, currentNames: state.proximaNames, currentOd: state.proximaNames[0] }
            }
            return state

        case RESET: 
        
         return { ...state, currentOd: state.currentNames[0] }

        default:
            return state;
    }



}