//CONSTS
export const LT_ERROR = 'LT собран неверно'


//A
const SET_LT = 'SET_LT'
const SET_FETCHED_LT = 'SET_FETCHED_LT'


//AC

export const setFetchedLt = ({ packages, value }) => ({ type: SET_FETCHED_LT, packages, value })
export const setLt = (currentComplect, currentContract) => ({ type: SET_LT, currentComplect, currentContract })





const legalTech = (state = initialState, action) => {

    switch (action.type) {

        case SET_FETCHED_LT:
            return { ...state, packages: action.packages, value: action.value }

        case SET_GLOBAL:

            let packages = { ...state.packages }

            for (const weight in packages) {

                packages[weight] = { ...packages[weight] }

                action.currentRegion.name === 'msk'

                    ? packages[weight].price = packages[weight].msk
                    : packages[weight].price = packages[weight].regions
            }

            return { ...state, packages }

        case SET_LT:
            //action: currentComplect
            //currentContract : { id, value, measureIndex, prepayment, discount }
            let setLtState = { ...state };
            setLtState.value = checkUncheckLt(setLtState.value, action.currentComplect);
            setLtState = weightLt(setLtState, action.currentComplect);
            
            
            if (setLtState.current) {//если есть текущий платный LT - расчитываем цену и продукт в зависимости от пришедшего контракта

                setLtState.current = getServicesPriceAndProduct(setLtState.current , action.currentContract)
            }

            return setLtState


        case SET_CONTRACT_IN_BLOCKS:
            //action: currentContract : { id, value, measureIndex, prepayment, discount }
            if (state.current && state.packages[state.current.weight]) {

                let resultPackage = { ...state.current }
                let searchingIndex = resultPackage.weight
                resultPackage = getServicesPriceAndProduct(state.packages[searchingIndex], action.currentContract)
              

                return { ...state, current: resultPackage }
            } else {
                return state
            }


        case RESET || RESET_UNIVERSAL:
            const resetState = uncheckBlocks([state])[0]

            return {
                ...resetState,
                'display': 'none',
                'weightLt': 0,
                'ltIncluded': 0,
            }


        // case TYPE_OF_CONTRACT:  //TODO ??????
        //     return changeLt(state)
        default:
            return state
    }


}

export default legalTech 