
import Selects from '../../Common/Selects/Selects';
import './Contract-Select.css';



const ContractSelect = (props) => {

    return <Selects
        name={'Тип договора'}
        items={props.contracts}
        currentValue={props.current ? props.current.value : 'Тип договора'}
        onSelectClick={props.changeTypeOfContract}
    />
}

export default ContractSelect

