

/////////FILLING INFOBLOCKS


export const fillingInfoblocks = (currentComplect, checked, value) => {

    //меняет текущее наполнение в currentComplect
    let resultState = currentComplect
    if (currentComplect) {
        resultState = { ...currentComplect, filling: [...currentComplect.filling] }

        if (currentComplect.isChanging) {
            if (checked) {   //надо отключить и checked сделать false
                resultState.filling.forEach((elem, idx) => {
                    if (elem === value) {
                        if (resultState.name === 'Предприятие PRO' && value === 'Справочник промышленника') {

                        } else if (value === 'Справочник нормативно-технической документации по строительству. Дополнительные материалы' ||
                            value === 'Справочник промышленника. Дополнительные материалы'
                        ) {

                        } else if (value === 'Справочник нормативно-технической документации по строительству') {

                            resultState.filling = resultState.filling.filter(infoblock => (infoblock !== 'Справочник нормативно-технической документации по строительству' && infoblock !== 'Справочник нормативно-технической документации по строительству. Дополнительные материалы'))
                        } else if (value === 'Справочник промышленника') {

                            resultState.filling = resultState.filling.filter(infoblock => (infoblock !== 'Справочник промышленника' && infoblock !== 'Справочник промышленника. Дополнительные материалы'))
                        } else {
                            resultState.filling.splice(idx, 1)
                        }
                    }
                })

                //Если изменяются блоки от которых зависят блоки из других разделов


                if (value === 'Практика арбитражных апелляционных судов округов' || value === 'Практика арбитражных судов округов') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 8)
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 9)
                }
                if (value === 'Практика судов общей юрисдикции') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 6)
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 7)
                }
                if (value === 'Практика высших судебных органов') {
                    resultState.freeBlocks = fillingFreeblocksWithUncheck(resultState, 6)
                }



            } else {
                if (value === 'Справочник нормативно-технической документации по строительству. Дополнительные материалы' ||
                    value === 'Справочник промышленника. Дополнительные материалы'
                ) {

                } else if (value === 'Справочник нормативно-технической документации по строительству') {
                    resultState.filling.push(value)
                    resultState.filling.push('Справочник нормативно-технической документации по строительству. Дополнительные материалы')
                } else if (value === 'Справочник промышленника') {

                    resultState.filling.push(value)
                    resultState.filling.push('Справочник промышленника. Дополнительные материалы')
                } else {
                    resultState.filling.push(value)
                }

                if (value === 'Практика арбитражных апелляционных судов округов') {
                    if (!resultState.freeBlocks.includes(8) || !resultState.freeBlocks.includes(9)) {
                        resultState.freeBlocks.push(8)
                        resultState.freeBlocks.push(9)
                    }


                }
                if (value === 'Практика судов общей юрисдикции') {
                    if (!resultState.freeBlocks.includes(6) || !resultState.freeBlocks.includes(7)) {
                        resultState.freeBlocks.push(6)
                        resultState.freeBlocks.push(7)
                    }
                }
                if (value === 'Практика высших судебных органов') {
                    if (!resultState.freeBlocks.includes(6)) {
                        resultState.freeBlocks.push(6)

                    }
                }

            }
        }
    }

    return resultState
}


/////////FILLING ENCYCLOPEDIAS
export const fillingEr = (currentComplect, packets, checked, index) => { //для ЭР и Пакетов в зависимости от параметров если checked делает uncheck  - заходит в state и убирает из filling индексов

    //ersInPacket

    let ers = [...currentComplect.ers]
    let packetsInComplect = [...currentComplect.packetsEr]
    let ersInPacketsInComplect = [...currentComplect.ersInPacket]

    let activePackets = packets.filter((pac, i) => packetsInComplect.includes(i))



    if (checked === true) {  //если надо отключить ЭР
        // if (ers.length > -1) {

        ers = ers.filter(er => er !== index)
        if (currentComplect.name !== 'Офис' && ersInPacketsInComplect.includes(index)) { //если  комплект не Офис и если ЭР содержится в пакете
            // если ЭР которую отключают входит в активный пакет - отключить пакет, а оставшиеся из него ЭР - положить в ЭР
            packets.forEach((packet, packetIndex) => { //перебираем пакеты (не индексы, а из encyclopedia-reducer)

                if (packet.including.includes(index) && activePackets.some(activePacket => activePacket.name === packet.name)) {           //если индекс отключаемой ЭР есть в перебираемом пакете и этот пакет включен в комплект 
                    packetsInComplect = packetsInComplect.filter(packetInComplect => packetInComplect !== packetIndex) //убираем индекс этого пакета из currentComplect
                    ersInPacketsInComplect = ersInPacketsInComplect.filter(erInPacket => !packet.including.includes(erInPacket)) //убираем пакеты входящие в комплект из currentComplect.ersInPacket
                    const newPacketsInComplect = packet.including.filter(erIndex => erIndex !== index)  //создаём новый массив индексов из индексов ЭР входящих в пакет, но без отклюючаемой ЭР, чтобы закинуть это потом в currentComplect.ers

                    ers = [...new Set(ers.concat(newPacketsInComplect))];
                    //объединяем массивы arr1 и arr2 с помощью метода concat. Затем мы создаем новый массив, используя спред-оператор и конструктор Set, чтобы удалить дубликаты. 
                    console.log(ers)



                }

            })


        } else if (currentComplect.name === 'Офис' && !ersInPacketsInComplect.includes(index) || (currentComplect.name != 'Офис' && !ersInPacketsInComplect.includes(index))) { //если Офис/НЕОФИС и если ЭР не входит в пакет
            ers = ers.filter(er => er !== index)
        }
    } else {

        if (!ers.includes(index)) ers.push(index)
    }


    return {
        ...currentComplect,
        ers: ers,
        packetsEr: packetsInComplect,
        ersInPacket: ersInPacketsInComplect
    }
}


export const fillingPketsEr = (currentComplect, packets, checked, index) => { //меняет currentComplect
    let packetsEr = [...currentComplect.packetsEr]
    let ers = [...currentComplect.ers]
    let ersInPacketsInComplect = [...currentComplect.ersInPacket]
    const defaultErInPacket = packets[index].including

    const changePaketsInState = (checked, index) => { // если текущий комплект не офис для Пакетов в зависимости от параметров если checked делает uncheck в дате allEnciclopedis - заходит в state и убирает из filling индексов

        if (checked === true) {
            packetsEr = [] // - очищает информацию о входящих в комплект пакетах в state.currentComplect
            ersInPacketsInComplect = []
        } else {
            packetsEr = [index]
            ersInPacketsInComplect = [...defaultErInPacket]

        }

        // changePaketsErFromCurrent(state);
    }
    const setErInPacketsEr = (indexesOfActivePackets, packets) => {
        //принимает индексы активных пакетов в комплекте и все пакеты (полностью информацию о них)
        //перебирает пакеты , если индекс пакета содержится в пришедшем массиве индексов активных пакетов
        //cклеивает к результату уникальные значения
        //возвращает массив уникальных символов для индексов входящих в состав Пакета Комплекта
        let result = []
        packets.forEach((pack, i) => {

            if (indexesOfActivePackets.includes(i)) {

                result = [...new Set(result.concat(pack.including))]
            }
        })
        return result
    }

    if (currentComplect.isChanging) {

        if (currentComplect.name !== 'Офис') {
            changePaketsInState(checked, index)
            // изменяет индекс входящих в комплект пакетов
            //TODO: if universal complect
        } else {

            window.alert('в комплекте Гарант-Офис должны содержаться два любых Пакета ЭР')
            if (checked === true) {
                if (index === 0) {
                    packetsEr = [1, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 1) {
                    packetsEr = [0, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 2) {
                    packetsEr = [0, 1]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                }

            } else { //checked == false
                if (index === 0) {
                    packetsEr = [0, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 1) {
                    packetsEr = [0, 1]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                } else if (index === 2) {
                    packetsEr = [1, 2]
                    ers = []
                    ersInPacketsInComplect = setErInPacketsEr(packetsEr, packets)
                }
            }
        }
    }
    // 0, 2, 4, 5, 6, 0, 3, 4, 6, 7
    //взять массив входящих в пакет энциклопедий и отфильтровать в соотвествии с ним :
    ers = ers.filter((item) => !defaultErInPacket.includes(item))
    currentComplect.packetsEr = packetsEr
    currentComplect.ers = ers
    currentComplect.ersInPacket = ersInPacketsInComplect
    return { ...currentComplect }
}


/////////FILLING FREEBLOCKS
export const fillingFreeblocksWithUncheck = (currentComplect, currentFreeblockIndex) => {
    let freeblocks = []

    currentComplect.freeBlocks.forEach(freeblockIndex => {
        if ((currentFreeblockIndex === 9 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 8 && currentFreeblockIndex === freeblockIndex)) {
            // если он // 9 - Онлайн-архив «Практика арбитражных судов первой инстанции» или
            // 8 - Онлайн-архив «Определения арбитражных судов»
            if (currentComplect.filling.includes('Практика арбитражных судов округов') || currentComplect.filling.includes('Практика арбитражных апелляционных судов округов')) { //если комплект не содержит то от чего он зависит
                //если в комплекте есть то, от чего он зависит, пушим в новый массив
                freeblocks.push(freeblockIndex)

            }
        } else if ((currentFreeblockIndex === 6 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 7 && currentFreeblockIndex === freeblockIndex)) {
            // 6 - Онлайн-архив «Практика судов общей юрисдикции»   или
            // 7 - Онлайн-архив «Практика мировых судей»
            if (currentComplect.filling.includes('Практика судов общей юрисдикции')) { //если комплект не содержит то от чего он зависит
                //если в комплекте есть то, от чего он зависит, пушим в новый массив
                freeblocks.push(freeblockIndex)

            }
        } else if (currentFreeblockIndex === 6 && currentFreeblockIndex === freeblockIndex) {
            // 6 - Онлайн-архив «Практика судов общей юрисдикции»
            if (currentComplect.filling.includes('Практика высших судебных органов')) {
                //если в комплекте есть то, от чего он зависит, пушим в новый массив
                freeblocks.push(freeblockIndex)
            }


            // 10 - Правовой консалтинг. Премиум: База знаний службы Правового консалтинга
            // 11 - 2 экспертных заключения в месяц
            // 12 - Неограниченное количество устных консультаций
        } else if ((currentFreeblockIndex === 10 && currentFreeblockIndex === freeblockIndex) || (currentFreeblockIndex === 12 && currentFreeblockIndex === freeblockIndex)) {


            if (currentComplect.consaltingProduct.includes(1) || currentComplect.consaltingProduct.includes(2)) {  //советы экспертов

                freeblocks.push(freeblockIndex)
            }

        } else if ((currentFreeblockIndex === 11 && currentFreeblockIndex === freeblockIndex)) {
            if (currentComplect.consaltingProduct.includes(2)) {  //Правовой консалтинг премиум


                freeblocks.push(freeblockIndex)
            }


        } else if (currentFreeblockIndex !== freeblockIndex) {
            //если это индексы-блоки, не те которые были отжаты, но те которые находились вфриблоках текущего комплекта
            freeblocks.push(freeblockIndex)
        }
    })


    return freeblocks
}





/////////FILLING LEGAL TECH

export const fillingLt = (comeCurrentComplect, checked, index) => {
    let currentComplect = { ...comeCurrentComplect }
    currentComplect.lt = [...currentComplect.lt]
    currentComplect.ltInPacket = [...currentComplect.ltInPacket]

    const currentWeightOfIncludedLT = (currentComplect) => {
        let currentWeight = 0
        currentWeight = currentComplect.lt.length

        return currentWeight
    }

    if (currentComplect) {
        if (currentComplect.name === 'Офис') {
            // let currentWeight
            let currentWeightOfIncluded
            if (currentComplect.lt.includes(index) === false) { //если сервис НЕ входит в инклудед - по умолчанию в комплект
                if (checked === false) {

                    currentWeightOfIncluded = currentWeightOfIncludedLT(currentComplect)
                    currentWeightOfIncluded >= 5 ? currentComplect.ltInPacket.push(index) : currentComplect.lt.push(index) //если 5 и более пушит в доп LT если нет, то в included


                } else {

                    currentComplect.ltInPacket.forEach((elem, idx) => {

                        if (elem === index) {
                            currentComplect.ltInPacket.splice(idx, 1)
                        }


                    })
                }
            } else { //если сервис входит в инклудед - по умолчанию в комплект
                if (checked === false) {

                } else {
                    // state.legalTech.value[action.index].checked = false
                    currentComplect.lt.forEach((elem, idx) => {
                        if (elem === index) {
                            currentComplect.lt.splice(idx, 1)
                        }

                    })
                    currentWeightOfIncluded = currentWeightOfIncludedLT(currentComplect)

                    if (currentWeightOfIncluded < 5) { //если included <5 а в доп сервисах что-то есть - переносим из доп в инклудед
                        if (currentComplect.ltInPacket.length > 0) {
                            currentComplect.lt.push(currentComplect.ltInPacket[currentComplect.ltInPacket.length - 1]) //пушим в инклудед последний индекс из доп
                            currentComplect.ltInPacket.splice(currentComplect.ltInPacket.length - 1, 1) //и вырезаем его из доп
                        } else {
                            window.alert(`В комплекте Гарант-Офис должно быть минимум 5 сервисов Legal Tech, сейчас их ${currentWeightOfIncluded} в комплекте`)

                        }
                    }
                }
            }

        } else if (currentComplect.name !== 'Эксперт PRO') {

            if (currentComplect.lt.includes(index) === false) {
                if (checked === false) {
                    currentComplect.ltInPacket.push(index)
                } else {

                    currentComplect.ltInPacket.forEach((elem, idx) => {
                        if (elem === index) {
                            currentComplect.ltInPacket.splice(idx, 1)
                        }
                    })
                }
            }
        } else {
            if (currentComplect.name === 'Эксперт PRO' && index !== 3) {
                // if (currentComplect.ltInPacket.length === 0) {
                if (checked) {
                    if (currentComplect.ltInPacket.length === 0) {
                        currentComplect.lt = [3]
                    } else {
                        if (currentComplect.lt.includes(index)) {
                            currentComplect.lt = currentComplect.lt.filter(ltIndex => ltIndex != index)
                        }
                        if (currentComplect.ltInPacket.includes(index)) {
                            currentComplect.ltInPacket = currentComplect.ltInPacket.filter(ltIndex => ltIndex != index)
                        }
                    }




                } else {
                    if (currentComplect.lt.length > 1) {
                        currentComplect.ltInPacket.push(index)
                    } else {
                        currentComplect.lt.push(index)
                    }
                }
                // }


            }
        }
    }

    return { ...currentComplect }


}

export const fillingConsalting = (currentComplect, checked, index) => {
    if (index !== 0 && currentComplect && !currentComplect.withConsalting) { //если это не Горячая Линия и текущий уомплект есть//если в комплекте не идет консалтинг по умолчанию
        let resultCurrentComplect = { ...currentComplect }
        resultCurrentComplect.consalting = [...currentComplect.consalting]
        resultCurrentComplect.consaltingProduct = [...currentComplect.consaltingProduct]

        if (checked) { //если надо деактивировать

            resultCurrentComplect.consaltingProduct.includes(index) ?
                resultCurrentComplect.consaltingProduct = []
                : resultCurrentComplect.consaltingProduct = resultCurrentComplect.consaltingProduct


            resultCurrentComplect.freeBlocks = resultCurrentComplect.freeBlocks.filter(blockIndex => blockIndex !== 12 && blockIndex !== 11 && blockIndex !== 10)


        } else { //если надо активировать

            resultCurrentComplect.consaltingProduct = [index]

            if (index === 1) {
                [10, 12].forEach(blockIndex => !resultCurrentComplect.freeBlocks.includes(blockIndex) && resultCurrentComplect.freeBlocks.push(blockIndex))
                if (resultCurrentComplect.freeBlocks.includes(11)) {
                    resultCurrentComplect.freeBlocks = resultCurrentComplect.freeBlocks.filter(blockIndex => blockIndex !== 11)
                }

            } else if (index === 2) {
                [10, 11, 12].forEach(blockIndex => !resultCurrentComplect.freeBlocks.includes(blockIndex) && resultCurrentComplect.freeBlocks.push(blockIndex))
            }

        }

        debugger
        return resultCurrentComplect
    } else {
        return currentComplect
    }

}